@import "../../variables";

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}

#home {
  margin: 1em;
}
#user-detail-name {
  overflow: hidden;
  font-size: 6.5em;
  border-right: 0.15em solid orange;
  white-space: nowrap;
  animation: typing 3.5s steps(18, end), blink-caret 0.75s step-end infinite;
}
